import "./about.css";
import image from './nzm.png'

const About = () => {

    const downloadFile = () => {
        const link = document.createElement('a');
        link.href = 'https://drive.google.com/uc?export=download&id=14dcklA-OcW7DefHueU61ce4QaKjaWChc';
        link.download = 'Mohammed Nizam Resume'; // Replace 'filename' with the desired file name
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      };

    return (
        <div className="about">
            <div className="image" data-animate="animate-left">
                <img src={image} alt="" />
            </div>
            <div className="content" data-animate="animate-right">
                <h1> <span>{"<"}</span>About<span>{"/>"}</span></h1>
                <p>
                I am a dedicated UI/UX Designer and Frontend Developer passionate about crafting captivating digital experiences. I excel at turning complex challenges into intuitive, visually stunning solutions, with a portfolio featuring immersive web interfaces. 
                Driven by empathy, creativity, and a relentless pursuit of excellence, I focus on user-centered design and seamless frontend implementation. Constantly pushing boundaries and staying ahead of trends, I’m committed to creating work that elevates user engagement and satisfaction.
                </p>
                {/* <p>
                    I'm a dedicated UI/UX designer passionate about creating captivating digital experiences. With a focus on user-centered design and collaboration, I thrive on transforming complex challenges into intuitive solutions. My portfolio showcases a diverse array of projects, each driven by empathy, creativity, and a relentless pursuit of excellence. From sleek mobile apps to immersive web interfaces, I aim to elevate user engagement and satisfaction through thoughtful design.
                    Constantly seeking to push boundaries and stay ahead of trends, I'm committed to continuous learning and growth in this ever-evolving field. Let's connect and explore how we can turn your vision into reality!
                </p> */}

                <button onClick={downloadFile} className="botmBtn">Resume <i class='bx bx-down-arrow-circle'></i></button>
            </div>
        </div>
    );
};

export default About;
