import React, { useState } from "react";
import "./projects.css";
import cineImg from "./images/cinematriximg.png";
import ybImg from "./images/yboximg.png";
import gjImg from "./images/getjobimg.png";
import pnImg from "./images/polynoteimg.png";

const Projects = () => {
    const [showDef, setShowDef] = useState(false);

    const projectData = [
        {
            heading: "Cinematrix",
            image: cineImg,
            link: "https://cinematrix-web.web.app/",
            tech: ["React", "Firebase"],
            definition:
                "Cinematrix, built with React.js and Firebase, lets movie enthusiasts explore a vast database with real-time search, user authentication, personalized watchlists, and detailed movie information for an engaging experience.",
        },
        {
            heading: "yBox Chatapp",
            image: ybImg,
            link: "https://ybox-web.netlify.app/",
            tech: ["React", "Firebase"],
            definition:
                "yBox, built with React.js and Firebase, ensures seamless communication with instant message delivery, user authentication, secure data storage, and a sleek, intuitive design for an optimal real-time chat experience.",
        },
        {
            heading: "getJob",
            image: gjImg,
            link: "https://getjob-84bbb.web.app/",
            tech: ["React", "Firebase"],
            definition:
                "getJob, built with React.js and Firebase for our college project, lets job seekers and employers explore a vast job database. Features include real-time search, user authentication, and detailed descriptions.",
        },
        {
            heading: "SSMPOLY-Notes",
            image: pnImg,
            link: "https://sfissmptc.github.io/polynotes-sfissmptc/",
            tech: ["HTML", "CSS", "JS"],
            definition:
                "SSMPOLY - Notes is a website for polytechnic engineering students, built with HTML, CSS, and JavaScript. It offers organized academic notes with links to Drive files for easy access.",
        },
    ];

    // const techColors = {
    //     react : {
    //        color : "#0046bd99",
    //        backgroundColor : "#6bc6ff"
    //     },
    // }

    return (
        <div className="projects">
            <h1> <span>{"<"}</span>Projects<span>{"/>"}</span></h1>

            <div className="proj-cards">
                {projectData.map((project, index) => (
                    <div className="project" key={index}>
                        {showDef !== index && (
                            <>
                                {" "}
                                <img src={project.image} alt="" />
                                <div className="head-link">
                                    <h3>{project.heading}</h3>
                                    <a href={project.link} target="_blank" rel="noreferrer">
                                        <i class="material-symbols-outlined">link</i>
                                    </a>
                                </div>{" "}
                            </>
                        )}
                        <div className="tech-info">
                            {showDef !== index && (
                                <div className="techs">
                                    {project.tech.map((tech, index) => (
                                        <p key={index}>{tech}</p>
                                    ))}
                                </div>
                            )}
                            <i
                                onClick={() => setShowDef(showDef === index ? null : index)}
                                className={`bx ${showDef === index ? "bx-x" : "bx-info-circle"}`}
                            ></i>
                        </div>

                        {/* Show definition only for the clicked project */}
                        {showDef === index && <p className="definition">{project.definition}</p>}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Projects;
