import React, { useEffect, useState } from "react";
import "./home.css";

const Home = () => {
    const targetText = "<MOHAMMED NIZAM>"; // Split the text into an array of characters
    const [displayedName, setDisplayedName] = useState(new Array(targetText.length));
    const totalDuration = 4000; // Increase to 4 seconds (in milliseconds)
    const totalFrames = 120; // Increase the number of frames for smoother animation
    const frameDuration = totalDuration / totalFrames; // Time between frames
    const characters = "A!B@C#D$E%F&G*H(I)J=K+L-M~N[O?PQ>RS]TU<V{WXY}Z"; // Characters to shuffle through

    useEffect(() => {
        let currentFrame = 0;

        const shuffleText = () => {
            const tempText = [...displayedName]; // Create a copy of the current displayed text

            for (let i = 0; i < targetText.length; i++) {
                // If we haven't reached the final letter, shuffle the characters
                if (currentFrame < (i + 1) * (totalFrames / targetText.length)) {
                    tempText[i] = characters[Math.floor(Math.random() * characters.length)];
                } else {
                    // Show the correct letter once we've reached the frame for it
                    tempText[i] = targetText[i];
                }
            }

            setDisplayedName(tempText); // Update the state with the new shuffled text
            currentFrame++;
            if (currentFrame <= totalFrames) {
                setTimeout(shuffleText, frameDuration); // Use setTimeout to control the animation speed
            }
        };

        // Start the animation
        shuffleText();
    }, []);

    const [displayedText, setDisplayedText] = useState("");
    const [currentIndex, setCurrentIndex] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [phase, setPhase] = useState(1);

    const texts = ["UI/UX Designer", "Web Developer"];

    useEffect(() => {
        let timeout;

        const currentText = texts[phase - 1];

        if (!isDeleting) {
            // Typing Phase
            if (currentIndex < currentText.length) {
                timeout = setTimeout(() => {
                    setDisplayedText(currentText.substring(0, currentIndex + 1));
                    setCurrentIndex(currentIndex + 1);
                }, 200); // Typing speed
            } else {
                // Pause after typing full text
                timeout = setTimeout(() => setIsDeleting(true), 1000);
            }
        } else {
            // Backspace Phase
            if (currentIndex > 0) {
                timeout = setTimeout(() => {
                    setDisplayedText(currentText.substring(0, currentIndex - 1));
                    setCurrentIndex(currentIndex - 1);
                }, 100); // Backspace speed
            } else {
                // Move to the next phase after backspacing
                setIsDeleting(false);
                setPhase(phase === texts.length ? 1 : phase + 1); // Loop back to the first text
            }
        }

        return () => clearTimeout(timeout);
    }, [currentIndex, isDeleting, phase]);

    return (
        <div className="home">
            <div className="top-gradient"></div>
            <div className="name">
                <div className="name-container">
                    {displayedName.map((letter, index) => (
                        <div key={index} className={`letter ${letter === targetText[index] ? "active" : "inactive"}`}>
                            <p>{letter}</p>
                        </div>
                    ))}
                </div>
                <div className="role">{phase <= texts.length && <h4>{displayedText}</h4>}</div>
            </div>
            <div className="arrow-icon" onClick={() => window.scroll(0, 600)}>
                <span>{"~>"}</span>
            </div>
        </div>
    );
};

export default Home;
