import React, { useState } from "react";
import "./contact.css";
import axios from "axios";

const Contact = () => {
    const [formData, setFormData] = useState({
        name: "",
        email: "",
        mobile: "",
        message: "",
    });
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState({ message: "", stat: 0 });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        try {
            const response = await axios.post("https://script.google.com/macros/s/AKfycbzk_2BiblDLM9Drmiax-HydLDjGejaZKrGxiKevud-_5KsrfZuHmnINvq02osZcgjQUHw/exec", new URLSearchParams(formData));
            if (response.status === 200) {
                // setSuccess(true);
                setMessage({ message: "The message has been sent successfully.", stat: 0 });
                setFormData({
                    name: "",
                    email: "",
                    mobile: "",
                    message: "",
                });

                setLoading(false);
            }
        } catch (error) {
            console.log(error);
            setMessage({ message: "An occured, try again", stat: 1 });
        } finally {
            setTimeout(() => {
                setMessage({ message: "", stat: 0 });
            }, 3000);
        }
    };

    return (
            <div className="contact">
            <h1> <span>{"<"}</span>ConatctMe<span>{"/>"}</span></h1>

                <div className="contact-form-div">
                    <form className="contact-form" id="submit-form" action="" onSubmit={handleSubmit}>
                        <input className="form-name" type="text" id="name" name="name" placeholder="Name" value={formData.name} onChange={handleChange} required />
                        <span className="form-span">
                            <input className="form-email" type="email" id="email" name="email" placeholder="Email" value={formData.email} onChange={handleChange} required />

                            <input className="form-mobile" type="tel" id="mobile" name="mobile" placeholder="Mobile" value={formData.mobile} onChange={handleChange} required />
                        </span>

                        <textarea className="form-text" rows="5" id="message" name="message" placeholder="Message" value={formData.message} onChange={handleChange} required></textarea>

                        <button className="submit" type="submit" value="Submit">
                            <span className="material-symbols-outlined">mail</span> {loading ? "Sending" : "Send Message"}
                        </button>
                    </form>

                    {message.message && (
                        <div className="message" style={message.stat === 0 ? { backgroundColor: "#005bc9" } : { backgroundColor: "#e70051" }}>
                            <i className="material-symbols-outlined">priority</i> <p>{message.message}</p>
                        </div>
                    )}

                    <div className="social-media">
                        <a href="http://wa.me/+917907932464" target="_blank" rel="noreferrer">
                            <div className="mini-card">
                                <i class="bx bxl-whatsapp"></i>
                            </div>
                        </a>
                        <a href="https://www.instagram.com/nizam__._/" target="_blank" rel="noreferrer">
                            <div className="mini-card">
                                <i class="bx bxl-instagram"></i>
                            </div>
                        </a>
                        <a href="https://github.com/Nizam-324" target="_blank" rel="noreferrer">
                            <div className="mini-card">
                                <i class="bx bxl-github"></i>
                            </div>
                        </a>
                        <a href="https://www.linkedin.com/in/moh-nizam" target="_blank" rel="noreferrer">
                            <div className="mini-card">
                                <i class="bx bxl-linkedin"></i>
                            </div>
                        </a>
                        <a href="mailto:mohammednizam.in@gmail.com" target="_blank" rel="noreferrer">
                            <div className="mini-card mail">
                                <i class="bx bxl-google"></i>
                                <p>mohammednizam.in@gmail.com</p>
                            </div>
                        </a>
                    </div>
                </div>
            </div>
    );
};

export default Contact;
