import React from "react";
import "./skills.css";

const SkillsService = () => {
    const skills = [
        {
            skill: "HTML",
            perc: 90,
        },
        {
            skill: "JavaScript",
            perc: 80,
        },
        {
            skill: "CSS",
            perc: 85,
        },
        {
            skill: "React",
            perc: 78,
        },
        {
            skill: "Python",
            perc: 74,
        },
        {
            skill: "Django",
            perc: 65,
        },
    ];

    const screenWidth = window.innerWidth <= 834;

    return (
        <div style={{ width: screenWidth ? "unset" : "1100px", display: "flex", flexDirection: screenWidth ? "column" : "", margin: "0 auto" }}>
            <div className="skills">
                <h1> <span>{"<"}</span>Skills<span>{"/>"}</span></h1>
                <div className="skillset">
                    {skills.map((skill, index) => (
                        <div className="skill">
                            <span style={{ width: `${skill.perc}%` }}></span>
                            <i class="material-symbols-outlined">verified</i> <p>{skill.skill}</p>
                            <div className="right">
                                <p>{skill.perc}%</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
            <div className="service">
            <h1> <span>{"<"}</span>Service<span>{"/>"}</span></h1>
            <div className="serve-cards">
                    <div className="serve-card">
                        <h3>Web Development</h3>
                        <p>
                            Frontend development involves creating user interfaces with HTML, CSS, and JavaScript. React's component-based
                            architecture improves efficiency. I use these technologies to build responsive, intuitive web applications that enhance
                            user experiences.
                        </p>
                    </div>
                    <div className="serve-card">
                        <h3>UI/UX Designer</h3>
                        <p>
                            UI design in Figma involves crafting visually engaging interfaces with precision. Figma's platform enables seamless
                            collaboration, swift iteration, and efficient prototyping. Its intuitive tools and real-time collaboration bring visions
                            to life.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SkillsService;
